'use client';

import { useTheme } from 'next-themes';
import { AnimatePresence, motion } from 'framer-motion';
import SunIcon from '../icons/SunIcon';
import MoonIcon from '../icons/MoonIcon';
import {useEffect} from "react";

export default function ThemeSwitcher() {
    const { setTheme, resolvedTheme,theme } = useTheme();
    const isMounted = typeof window !== 'undefined' && resolvedTheme !== undefined;
    const isDark = resolvedTheme === 'dark';
    
    console.log(theme)
    useEffect(() => {

        const localTheme = localStorage.getItem('theme');
        if (localTheme === 'dark') {
            console.log('dark');
        } else if (localTheme === 'light') {
            console.log('light');
        }

    }, [resolvedTheme]);

    const handleClick = () => {
        setTheme(isDark ? 'light' : 'dark');
    };

    return (
        <button
            type="button"
            aria-label={isDark ? 'Switch to light mode' : 'Switch to dark mode'}
            onClick={handleClick}
            className="h-8 w-8 flex items-center justify-center rounded-full p-2 transition-colors duration-300 hover:bg-neutral-200 dark:hover:bg-neutral-800"
        >
            <AnimatePresence mode="wait" initial={false}>
                <motion.span
                    key={isMounted ? resolvedTheme : 'placeholder'}
                    initial={{ opacity: 0, rotate: -90, scale: 0.7 }}
                    animate={{ opacity: 1, rotate: 0, scale: 1 }}
                    exit={{ opacity: 0, rotate: 90, scale: 0.7 }}
                    transition={{ duration: 0.3 }}
                >
                    {!isMounted ? (
                        // üîπ –ó–∞–≥–ª—É—à–∫–∞: —Å–µ—Ä—ã–π –∫—Ä—É–∂–æ–∫ —Ç–æ–≥–æ –∂–µ —Ä–∞–∑–º–µ—Ä–∞
                        <div className="h-6 w-6 rounded-full bg-transparent dark:bg-transparent animate-pulse" />
                    ) : isDark ? (
                        <SunIcon className="h-6 w-6 transition duration-300" />
                    ) : (
                        <MoonIcon className="h-6 w-6 transition duration-300" />
                    )}
                </motion.span>
            </AnimatePresence>
        </button>
    );
}
